import React, { useRef } from 'react'
import styled from 'styled-components'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useToggleModal } from '../../state/application/hooks'
import i18next from '../../i18n'
// import ch from '../../assets/images/ch.png'
// import en from '../../assets/images/en.png'
// import ko from '../../assets/images/ko.png'

// import { ExternalLink } from '../../theme'
import { useTranslation } from 'react-i18next'
import xiangxia from '../../assets/images/xiangxia.png'
import xiangshang from '../../assets/images/xiangshang.png'
const StyledLanguageButton = styled.div`
  height: 35px;
  line-height: 35px;
  border-radius: 0.5rem;
  color: #75809F;
  font-size: 16px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
  margin-right:1rem;
  display: flex;
  align-items: center;
  svg {
    margin-top: 2px;
  }
  img{
    width: 14px;
    margin-left: 5px;
  }
`
const StyledLanguageText = styled.div`
  color: #fff;
  font-size: 14px;
`
const StyledLanguage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const LanguageFlyout = styled.span`
  min-width: 100px;
  background-color: #1a202d;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 5px;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 3rem;
  right: 0rem;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
  `};
`

const LanguageItem = styled.div`
  flex: 1;
  line-height: 36px;
  padding: 0;
  color: #fff;
  font-size: 14px;
  text-align: center
  :hover {
    background: rgba(26, 208, 153, 0.1);
    color: #1ad099;
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
  &.active {
    color:#1ad099;
  }
`
// const CODE_LINK = 'https://github.com/Uniswap/uniswap-interface'

export default function Language() {
  // const { account } = useActiveWeb3React()
  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.LANGUAGE)
  const toggle = useToggleModal(ApplicationModal.LANGUAGE)
  useOnClickOutside(node, open ? toggle : undefined)
  const { t } = useTranslation()
  // const openClaimModal = useToggleModal(ApplicationModal.ADDRESS_CLAIM)

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledLanguage ref={node as any}>
      <StyledLanguageButton onClick={toggle}>
        {/* {i18next.language === 'en' ? (
          <LogoBox>
            <img src={en} alt="logo" />
          </LogoBox>
        ) : ''}
        {i18next.language === 'zh-cn' ? (
          <LogoBox>
            <img src={ch} alt="logo" />
          </LogoBox>
        ) : ''}
        {i18next.language === 'ko' ? (
          <LogoBox>
            <img src={ko} alt="logo" />
          </LogoBox>
        ) : ''} */}
        <StyledLanguageText>{t('Language')}</StyledLanguageText>
         {open === false ? (
            <img src={xiangxia} alt="logo" />
        ) : 
        <img src={xiangshang} alt="logo" />
        }
      </StyledLanguageButton>

      {open && (
        <LanguageFlyout onClick={toggle}>
          <LanguageItem className={i18next.language === 'zh-cn' ? 'active' : ''} onClick={() => i18next.changeLanguage('zh-cn')}>
            {t('Chinese')}
          </LanguageItem>
          <LanguageItem className={i18next.language === 'en' ? 'active' : ''} onClick={() => i18next.changeLanguage('en')}>
            {t('English')}
          </LanguageItem>
          <LanguageItem className={i18next.language === 'ko' ? 'active' : ''} onClick={() => i18next.changeLanguage('ko')}>
            한국어
          </LanguageItem>
          {/* <LanguageItem className={i18next.language === 'ja' ? 'active' : ''} onClick={() => i18next.changeLanguage('ja')}>
             日本語
          </LanguageItem> */}
        </LanguageFlyout>
      )}
    </StyledLanguage>
  )
}
